/**
 *  Universal Module Enhancer
 *    
 * Applies enhancements to all modules automatically
 */

import systemEnhancer from './system-enhancer.js';
import aiAssistant from './ai-assistant.js';
import advancedCache from './advanced-cache.js';

class UniversalModuleEnhancer {
  constructor() {
    this.enhancedModules = new Set();
    this.init();
  }

  init() {
    // Wait for DOM to be ready
    if (document.readyState === 'loading') {
      document.addEventListener('DOMContentLoaded', () => {
        this.enhanceAllModules();
      });
    } else {
      this.enhanceAllModules();
    }

    // Enhance modules as they're created
    this.watchForNewModules();
  }

  /**
   * Enhance a single module
   */
  enhanceModule(moduleInstance, moduleName) {
    if (!moduleInstance || this.enhancedModules.has(moduleName)) {
      return;
    }

    try {
      // Apply System Enhancer
      if (systemEnhancer) {
        systemEnhancer.enhanceModule(moduleInstance);
      }

      // Add AI Assistant integration
      if (aiAssistant && moduleInstance.render) {
        const originalRender = moduleInstance.render.bind(moduleInstance);
        moduleInstance.render = (...args) => {
          // Get AI suggestions before rendering
          const suggestions = aiAssistant.getSuggestions({
            currentModule: moduleName,
          });

          // Store suggestions for UI
          if (moduleInstance.aiSuggestions === undefined) {
            moduleInstance.aiSuggestions = [];
          }
          moduleInstance.aiSuggestions = suggestions;

          // Render with enhancements
          const result = originalRender(...args);

          // Add AI suggestions to UI if available
          this.addAISuggestionsToUI(moduleInstance, suggestions);

          return result;
        };
      }

      // Add Advanced Cache integration
      if (advancedCache && moduleInstance.loadData) {
        const originalLoadData = moduleInstance.loadData.bind(moduleInstance);
        moduleInstance.loadData = async (...args) => {
          const cacheKey = `${moduleName}_${JSON.stringify(args)}`;

          // Try to get from cache
          const cached = advancedCache.get(cacheKey);
          if (cached !== null) {
            return cached;
          }

          // Fetch and cache
          const data = await originalLoadData(...args);
          advancedCache.set(cacheKey, data, 5 * 60 * 1000); // 5 minutes TTL

          return data;
        };
      }

      // Add performance monitoring
      if (moduleInstance.render) {
        const originalRender = moduleInstance.render.bind(moduleInstance);
        moduleInstance.render = (...args) => {
          const start = performance.now();
          const result = originalRender(...args);
          const end = performance.now();

          // Track render time
          if (moduleInstance.metrics === undefined) {
            moduleInstance.metrics = { renderTimes: [] };
          }
          moduleInstance.metrics.renderTimes.push(end - start);

          // Keep only last 100 render times
          if (moduleInstance.metrics.renderTimes.length > 100) {
            moduleInstance.metrics.renderTimes.shift();
          }

          return result;
        };
      }

      // Mark as enhanced
      this.enhancedModules.add(moduleName);
      console.log(` Enhanced module: ${moduleName}`);
    } catch (error) {
      console.error(` Error enhancing module ${moduleName}:`, error);
    }
  }

  /**
   * Add AI suggestions to UI
   */
  addAISuggestionsToUI(moduleInstance, suggestions) {
    if (!suggestions || suggestions.length === 0) return;

    // Find container
    const container =
      document.getElementById(`${moduleInstance.constructor.name.toLowerCase()}-container`) ||
      document.querySelector('.content-section') ||
      document.body;

    // Create suggestions panel if it doesn't exist
    let suggestionsPanel = document.getElementById('ai-suggestions-panel');
    if (!suggestionsPanel) {
      suggestionsPanel = document.createElement('div');
      suggestionsPanel.id = 'ai-suggestions-panel';
      suggestionsPanel.className = 'ai-suggestions-panel';
      container.insertBefore(suggestionsPanel, container.firstChild);
    }

    // Render suggestions
    suggestionsPanel.innerHTML = `
      <div class="ai-suggestions-header">
        <h4><i class="fas fa-lightbulb"></i>  </h4>
        <button class="btn-close" onclick="this.parentElement.parentElement.style.display='none'">
          <i class="fas fa-times"></i>
        </button>
      </div>
      <div class="ai-suggestions-list">
        ${suggestions
          .slice(0, 3)
          .map(
            suggestion => `
          <div class="ai-suggestion-item priority-${suggestion.priority}">
            <div class="suggestion-icon">
              <i class="fas fa-${suggestion.type === 'action' ? 'bolt' : 'keyboard'}"></i>
            </div>
            <div class="suggestion-content">
              <h5>${suggestion.title}</h5>
              <p>${suggestion.description}</p>
            </div>
            <button class="btn btn-sm btn-primary" onclick="(${suggestion.action.toString()})()">
              <i class="fas fa-arrow-left"></i>
            </button>
          </div>
        `
          )
          .join('')}
      </div>
    `;
  }

  /**
   * Enhance all existing modules
   */
  enhanceAllModules() {
    // Find all module instances in window
    const modulePatterns = [/System$/, /Management$/, /Module$/, /Manager$/];

    for (const key in window) {
      if (window.hasOwnProperty(key)) {
        const value = window[key];

        // Check if it's a module instance
        if (value && typeof value === 'object' && value.constructor) {
          const className = value.constructor.name;

          // Check if it matches module patterns
          if (modulePatterns.some(pattern => pattern.test(className))) {
            this.enhanceModule(value, key);
          }
        }
      }
    }

    // Also check for common module names
    const commonModules = [
      'patientsSystem',
      'sessionsSystem',
      'appointmentsSystem',
      'billingSystem',
      'accounting',
      'riskManagement',
      'complianceManagement',
      'backupRestore',
      'systemHealth',
      'performanceOptimizer',
      'uiEnhancer',
      'advancedFeatures',
      'unifiedDashboard',
      'unifiedSearch',
      'unifiedReports',
      'smartNotifications',
    ];

    commonModules.forEach(moduleName => {
      if (window[moduleName]) {
        this.enhanceModule(window[moduleName], moduleName);
      }
    });
  }

  /**
   * Watch for new modules being created
   */
  watchForNewModules() {
    // Use MutationObserver to watch for new script tags
    const observer = new MutationObserver(mutations => {
      mutations.forEach(mutation => {
        mutation.addedNodes.forEach(node => {
          if (node.tagName === 'SCRIPT' && node.src) {
            // New script loaded, check for new modules after a delay
            setTimeout(() => {
              this.enhanceAllModules();
            }, 1000);
          }
        });
      });
    });

    observer.observe(document.body, {
      childList: true,
      subtree: true,
    });

    // Also check periodically
    setInterval(() => {
      this.enhanceAllModules();
    }, 10000); // Every 10 seconds
  }

  /**
   * Get enhancement statistics
   */
  getStats() {
    return {
      enhancedModules: this.enhancedModules.size,
      moduleNames: Array.from(this.enhancedModules),
      systemEnhancer: systemEnhancer ? systemEnhancer.getMetrics() : null,
      cache: advancedCache ? advancedCache.getStats() : null,
    };
  }
}

// Export singleton instance
const universalModuleEnhancer = new UniversalModuleEnhancer();

if (typeof window !== 'undefined') {
  window.universalModuleEnhancer = universalModuleEnhancer;
}

export default universalModuleEnhancer;
