/**
 *  Lazy Loader Utility
 *    
 *      
 */

class LazyLoader {
  constructor() {
    this.loadedModules = new Set();
    this.loadingPromises = new Map();
    this.moduleCache = new Map();
    this.intersectionObserver = null;
    this.init();
  }

  /**
   * Initialize lazy loader
   */
  init() {
    // Setup Intersection Observer for lazy loading images and components
    if ('IntersectionObserver' in window) {
      this.intersectionObserver = new IntersectionObserver(
        entries => {
          entries.forEach(entry => {
            if (entry.isIntersecting) {
              const target = entry.target;
              const loadAction = target.dataset.lazyLoad;
              if (loadAction) {
                this.executeLazyLoad(loadAction, target);
                this.intersectionObserver.unobserve(target);
              }
            }
          });
        },
        {
          rootMargin: '50px', // Start loading 50px before element is visible
          threshold: 0.1,
        }
      );
    }
  }

  /**
   * Lazy load module
   */
  async loadModule(modulePath, options = {}) {
    const cacheKey = modulePath;

    // Check if already loaded
    if (this.loadedModules.has(cacheKey)) {
      if (options.forceReload) {
        this.loadedModules.delete(cacheKey);
        this.moduleCache.delete(cacheKey);
      } else {
        return this.moduleCache.get(cacheKey);
      }
    }

    // Check if currently loading
    if (this.loadingPromises.has(cacheKey)) {
      return this.loadingPromises.get(cacheKey);
    }

    // Start loading
    const loadPromise = this.executeLoad(modulePath, options);
    this.loadingPromises.set(cacheKey, loadPromise);

    try {
      const module = await loadPromise;
      this.loadedModules.add(cacheKey);
      this.moduleCache.set(cacheKey, module);
      this.loadingPromises.delete(cacheKey);
      return module;
    } catch (error) {
      this.loadingPromises.delete(cacheKey);
      throw error;
    }
  }

  /**
   * Execute module load
   */
  async executeLoad(modulePath, options) {
    try {
      if (modulePath.endsWith('.js')) {
        // Dynamic import for ES modules
        const module = await import(modulePath);
        return module.default || module;
      } else if (modulePath.endsWith('.css')) {
        // Load CSS file
        return await this.loadCSS(modulePath);
      } else if (modulePath.startsWith('http')) {
        // Load external resource
        return await this.loadExternalResource(modulePath);
      } else {
        // Default: try dynamic import
        const module = await import(modulePath);
        return module.default || module;
      }
    } catch (error) {
      console.error(`Error loading module: ${modulePath}`, error);
      throw error;
    }
  }

  /**
   * Load CSS file
   */
  async loadCSS(href) {
    return new Promise((resolve, reject) => {
      // Check if already loaded
      const existingLink = document.querySelector(`link[href="${href}"]`);
      if (existingLink) {
        resolve(existingLink);
        return;
      }

      const link = document.createElement('link');
      link.rel = 'stylesheet';
      link.href = href;
      link.onload = () => resolve(link);
      link.onerror = () => reject(new Error(`Failed to load CSS: ${href}`));
      document.head.appendChild(link);
    });
  }

  /**
   * Load external resource
   */
  async loadExternalResource(url) {
    const response = await fetch(url);
    if (!response.ok) {
      throw new Error(`Failed to load resource: ${url}`);
    }
    return await response.text();
  }

  /**
   * Lazy load image
   */
  lazyLoadImage(imgElement, src, placeholder = null) {
    if (!imgElement) return;

    // Set placeholder if provided
    if (placeholder) {
      imgElement.src = placeholder;
    }

    // Use Intersection Observer if available
    if (this.intersectionObserver) {
      imgElement.dataset.lazyLoad = src;
      imgElement.dataset.lazyType = 'image';
      this.intersectionObserver.observe(imgElement);
    } else {
      // Fallback: load immediately
      imgElement.src = src;
    }
  }

  /**
   * Lazy load component
   */
  async lazyLoadComponent(container, componentPath, props = {}) {
    if (!container) return;

    // Show loading state
    container.innerHTML = '<div class="lazy-loading"> ...</div>';

    try {
      // Load component module
      const Component = await this.loadModule(componentPath);

      // Render component
      if (typeof Component === 'function') {
        const component = new Component(container, props);
        return component;
      } else if (Component.default && typeof Component.default === 'function') {
        const component = new Component.default(container, props);
        return component;
      } else {
        container.innerHTML = Component;
        return Component;
      }
    } catch (error) {
      console.error('Error loading component:', error);
      container.innerHTML = '<div class="lazy-load-error">  </div>';
      throw error;
    }
  }

  /**
   * Execute lazy load action
   */
  async executeLazyLoad(action, target) {
    const type = target.dataset.lazyType;

    if (type === 'image') {
      target.src = action;
      target.removeAttribute('data-lazy-load');
      target.removeAttribute('data-lazy-type');
    } else if (type === 'component') {
      await this.lazyLoadComponent(target, action);
    } else if (type === 'module') {
      await this.loadModule(action);
    }
  }

  /**
   * Preload module (load in background)
   */
  async preloadModule(modulePath) {
    // Load module but don't wait for it
    this.loadModule(modulePath).catch(error => {
      console.warn(`Preload failed for ${modulePath}:`, error);
    });
  }

  /**
   * Preload multiple modules
   */
  async preloadModules(modulePaths) {
    const promises = modulePaths.map(path => this.preloadModule(path));
    await Promise.allSettled(promises);
  }

  /**
   * Clear module cache
   */
  clearCache(modulePath = null) {
    if (modulePath) {
      this.loadedModules.delete(modulePath);
      this.moduleCache.delete(modulePath);
    } else {
      this.loadedModules.clear();
      this.moduleCache.clear();
    }
  }

  /**
   * Get loaded modules
   */
  getLoadedModules() {
    return Array.from(this.loadedModules);
  }

  /**
   * Check if module is loaded
   */
  isModuleLoaded(modulePath) {
    return this.loadedModules.has(modulePath);
  }

  /**
   * Get loading status
   */
  getLoadingStatus() {
    return {
      loaded: this.loadedModules.size,
      loading: this.loadingPromises.size,
      cached: this.moduleCache.size,
    };
  }
}

// Export singleton instance
const lazyLoader = new LazyLoader();

// Make available globally
if (typeof window !== 'undefined') {
  window.lazyLoader = lazyLoader;
}

export default lazyLoader;
