/**
 *  Environment Configuration
 *   
 */

/**
 * Get environment variable with fallback
 */
function getEnvVar(key, defaultValue = null) {
  // Check if running in browser
  if (typeof window !== 'undefined') {
    // Try to get from window config
    if (window.APP_CONFIG && window.APP_CONFIG[key]) {
      return window.APP_CONFIG[key];
    }

    // Try to get from meta tag
    const metaTag = document.querySelector(`meta[name="${key.toLowerCase()}"]`);
    if (metaTag) {
      return metaTag.getAttribute('content');
    }
  }

  // Try to get from process.env (for Vite)
  if (typeof process !== 'undefined' && process.env) {
    const viteKey = `VITE_${key}`;
    if (process.env[viteKey]) {
      return process.env[viteKey];
    }
    if (process.env[key]) {
      return process.env[key];
    }
  }

  return defaultValue;
}

/**
 * Get API Base URL
 */
export function getApiBaseUrl() {
  return getEnvVar('API_BASE_URL', 'http://localhost:3000/api');
}

/**
 * Get WebSocket URL
 */
export function getWebSocketUrl() {
  const apiBaseUrl = getApiBaseUrl();
  // Convert http:// to ws:// and https:// to wss://
  const wsUrl = apiBaseUrl
    .replace(/^http:\/\//, 'ws://')
    .replace(/^https:\/\//, 'wss://')
    .replace(/\/api$/, '');

  return getEnvVar('WS_URL', wsUrl);
}

/**
 * Get Frontend URL
 */
export function getFrontendUrl() {
  if (typeof window !== 'undefined') {
    return window.location.origin;
  }
  return getEnvVar('FRONTEND_URL', 'http://localhost:5173');
}

/**
 * Get environment name
 */
export function getEnvironment() {
  return getEnvVar('NODE_ENV', 'development');
}

/**
 * Check if development
 */
export function isDevelopment() {
  return getEnvironment() === 'development';
}

/**
 * Check if production
 */
export function isProduction() {
  return getEnvironment() === 'production';
}

/**
 * Get client IP address
 */
export function getClientIp() {
  // In production, this should be obtained from the server
  // For now, return a placeholder
  if (isDevelopment()) {
    return '127.0.0.1';
  }

  // In production, try to get from headers or use a service
  // This is a placeholder - should be handled by backend
  return null;
}

/**
 * Configuration object
 */
export const ENV_CONFIG = {
  apiBaseUrl: getApiBaseUrl(),
  wsUrl: getWebSocketUrl(),
  frontendUrl: getFrontendUrl(),
  environment: getEnvironment(),
  isDevelopment: isDevelopment(),
  isProduction: isProduction(),
  apiTimeout: parseInt(getEnvVar('API_TIMEOUT', '30000'), 10),
  clientIp: getClientIp(),
};

export default ENV_CONFIG;
