/**
 *  API Client 
 *  Error Handling  Retry Logic  Interceptors
 */

import { API_CONFIG, API_ENDPOINTS } from '../config/api.js';

class ApiClient {
  constructor(config = API_CONFIG) {
    this.baseURL = config.baseURL;
    this.timeout = config.timeout;
    this.headers = { ...config.headers };
    this.retry = config.retry;

    // Request interceptors
    this.requestInterceptors = [];
    // Response interceptors
    this.responseInterceptors = [];
    // Error handlers
    this.errorHandlers = [];
  }

  /**
   * Add request interceptor
   */
  addRequestInterceptor(interceptor) {
    this.requestInterceptors.push(interceptor);
  }

  /**
   * Add response interceptor
   */
  addResponseInterceptor(interceptor) {
    this.responseInterceptors.push(interceptor);
  }

  /**
   * Add error handler
   */
  addErrorHandler(handler) {
    this.errorHandlers.push(handler);
  }

  /**
   * Get authentication token
   */
  getAuthToken() {
    if (typeof window !== 'undefined') {
      // Try new auth system
      if (typeof authSystem !== 'undefined') {
        const session = authSystem.getCurrentSession();
        if (session && session.token) {
          return session.token;
        }
      }

      // Fallback to localStorage
      return localStorage.getItem('adminToken');
    }
    return null;
  }

  /**
   * Apply request interceptors
   */
  async applyRequestInterceptors(config) {
    let finalConfig = { ...config };

    for (const interceptor of this.requestInterceptors) {
      finalConfig = await interceptor(finalConfig);
    }

    // Add auth token
    const token = this.getAuthToken();
    if (token) {
      finalConfig.headers = {
        ...finalConfig.headers,
        Authorization: `Bearer ${token}`,
      };
    }

    return finalConfig;
  }

  /**
   * Apply response interceptors
   */
  async applyResponseInterceptors(response) {
    let finalResponse = response;

    for (const interceptor of this.responseInterceptors) {
      finalResponse = await interceptor(finalResponse);
    }

    return finalResponse;
  }

  /**
   * Handle errors
   */
  async handleError(error, requestConfig) {
    // Apply error handlers
    for (const handler of this.errorHandlers) {
      await handler(error, requestConfig);
    }

    // Default error handling
    if (error.response) {
      // Server responded with error
      const { status, data } = error.response;

      switch (status) {
        case 401:
          // Unauthorized - redirect to login
          if (typeof window !== 'undefined') {
            if (typeof authSystem !== 'undefined') {
              authSystem.logout();
            } else {
              localStorage.removeItem('adminToken');
              localStorage.removeItem('adminUser');
              window.location.href = '/login.html';
            }
          }
          throw new Error('  -     ');

        case 403:
          throw new Error('      ');

        case 404:
          throw new Error('   ');

        case 500:
          throw new Error('   -   ');

        default:
          throw new Error(data?.message || `: ${status}`);
      }
    } else if (error.request) {
      // Request made but no response
      throw new Error('    -    ');
    } else {
      // Error in request setup
      throw new Error(error.message || '   ');
    }
  }

  /**
   * Retry request with exponential backoff
   */
  async retryRequest(requestFn, attempt = 1) {
    try {
      return await requestFn();
    } catch (error) {
      // Don't retry on 4xx errors (client errors)
      if (error.response && error.response.status >= 400 && error.response.status < 500) {
        throw error;
      }

      // Check if we should retry
      if (attempt >= this.retry.attempts) {
        throw error;
      }

      // Calculate delay with exponential backoff
      const delay = this.retry.delay * Math.pow(this.retry.backoff, attempt - 1);

      // Wait before retrying
      await new Promise(resolve => setTimeout(resolve, delay));

      // Retry
      return this.retryRequest(requestFn, attempt + 1);
    }
  }

  /**
   * Make HTTP request
   */
  async request(url, options = {}) {
    const requestConfig = {
      url: url.startsWith('http') ? url : `${this.baseURL}${url}`,
      method: options.method || 'GET',
      headers: {
        ...this.headers,
        ...options.headers,
      },
      body: options.body,
      timeout: options.timeout || this.timeout,
    };

    // Apply request interceptors
    const finalConfig = await this.applyRequestInterceptors(requestConfig);

    // Create request function
    const makeRequest = async () => {
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), finalConfig.timeout);

      try {
        const response = await fetch(finalConfig.url, {
          method: finalConfig.method,
          headers: finalConfig.headers,
          body: finalConfig.body ? JSON.stringify(finalConfig.body) : undefined,
          signal: controller.signal,
        });

        clearTimeout(timeoutId);

        // Parse response
        let data;
        const contentType = response.headers.get('content-type');
        if (contentType && contentType.includes('application/json')) {
          data = await response.json();
        } else {
          data = await response.text();
        }

        // Create response object
        const responseObj = {
          status: response.status,
          statusText: response.statusText,
          headers: response.headers,
          data,
          ok: response.ok,
        };

        // Check if response is ok
        if (!response.ok) {
          const error = new Error(response.statusText);
          error.response = responseObj;
          throw error;
        }

        // Apply response interceptors
        return await this.applyResponseInterceptors(responseObj);
      } catch (error) {
        clearTimeout(timeoutId);

        if (error.name === 'AbortError') {
          const timeoutError = new Error('  ');
          timeoutError.isTimeout = true;
          throw timeoutError;
        }

        throw error;
      }
    };

    // Execute request with retry
    try {
      return await this.retryRequest(makeRequest);
    } catch (error) {
      return await this.handleError(error, finalConfig);
    }
  }

  // Convenience methods
  async get(url, options = {}) {
    return this.request(url, { ...options, method: 'GET' });
  }

  async post(url, body, options = {}) {
    return this.request(url, { ...options, method: 'POST', body });
  }

  async put(url, body, options = {}) {
    return this.request(url, { ...options, method: 'PUT', body });
  }

  async patch(url, body, options = {}) {
    return this.request(url, { ...options, method: 'PATCH', body });
  }

  async delete(url, options = {}) {
    return this.request(url, { ...options, method: 'DELETE' });
  }
}

// Create singleton instance
const apiClient = new ApiClient();

// Add default request interceptor for logging (in development)
if (process.env?.NODE_ENV === 'development' || !process.env?.NODE_ENV) {
  apiClient.addRequestInterceptor(async config => {
    console.log(`[API] ${config.method} ${config.url}`);
    return config;
  });
}

// Add default response interceptor for logging (in development)
if (process.env?.NODE_ENV === 'development' || !process.env?.NODE_ENV) {
  apiClient.addResponseInterceptor(async response => {
    console.log('[API] Response:', response.status, response.data);
    return response;
  });
}

// Export singleton and class
export default apiClient;
export { ApiClient };
