/**
 *  Real-time Sync Manager
 *     
 */

import realtimeClient from './realtime-client.js';
import apiClient from './api-client.js';

class RealtimeSync {
  constructor() {
    this.syncHandlers = new Map();
    this.cache = new Map();
    this.subscriptions = new Set();

    this.init();
  }

  /**
   * Initialize real-time sync
   */
  init() {
    // Listen to real-time events
    realtimeClient.on('connected', () => {
      this.resubscribeAll();
    });

    // Listen to data update events
    realtimeClient.on('data-updated', data => {
      this.handleDataUpdate(data);
    });

    // Listen to notification events
    realtimeClient.on('notification', notification => {
      this.handleNotification(notification);
    });
  }

  /**
   * Subscribe to entity updates
   */
  subscribe(entityType, entityId, callback) {
    const key = `${entityType}:${entityId}`;

    if (!this.syncHandlers.has(key)) {
      this.syncHandlers.set(key, []);
    }

    this.syncHandlers.get(key).push(callback);

    // Join room for this entity
    realtimeClient.joinRoom(key);
    this.subscriptions.add(key);

    // Return unsubscribe function
    return () => {
      this.unsubscribe(entityType, entityId, callback);
    };
  }

  /**
   * Unsubscribe from entity updates
   */
  unsubscribe(entityType, entityId, callback) {
    const key = `${entityType}:${entityId}`;

    if (this.syncHandlers.has(key)) {
      const handlers = this.syncHandlers.get(key);
      const index = handlers.indexOf(callback);
      if (index > -1) {
        handlers.splice(index, 1);
      }

      if (handlers.length === 0) {
        this.syncHandlers.delete(key);
        realtimeClient.leaveRoom(key);
        this.subscriptions.delete(key);
      }
    }
  }

  /**
   * Handle data update
   */
  handleDataUpdate(data) {
    const { entityType, entityId, action, payload } = data;
    const key = `${entityType}:${entityId}`;

    // Update cache
    if (action === 'update' || action === 'create') {
      this.cache.set(key, payload);
    } else if (action === 'delete') {
      this.cache.delete(key);
    }

    // Notify handlers
    if (this.syncHandlers.has(key)) {
      this.syncHandlers.get(key).forEach(callback => {
        try {
          callback({ action, payload, entityType, entityId });
        } catch (error) {
          console.error(`Error in sync handler for ${key}:`, error);
        }
      });
    }
  }

  /**
   * Handle notification
   */
  handleNotification(notification) {
    // Emit global notification event
    window.dispatchEvent(
      new CustomEvent('realtime-notification', {
        detail: notification,
      })
    );
  }

  /**
   * Resubscribe to all rooms
   */
  resubscribeAll() {
    this.subscriptions.forEach(room => {
      realtimeClient.joinRoom(room);
    });
  }

  /**
   * Get cached data
   */
  getCached(entityType, entityId) {
    const key = `${entityType}:${entityId}`;
    return this.cache.get(key);
  }

  /**
   * Clear cache
   */
  clearCache(entityType = null) {
    if (entityType) {
      // Clear specific entity type
      for (const [key] of this.cache) {
        if (key.startsWith(`${entityType}:`)) {
          this.cache.delete(key);
        }
      }
    } else {
      // Clear all
      this.cache.clear();
    }
  }

  /**
   * Sync entity with server
   */
  async syncEntity(entityType, entityId) {
    try {
      const response = await apiClient.get(`/${entityType}/${entityId}`);
      const key = `${entityType}:${entityId}`;
      this.cache.set(key, response.data);

      // Notify handlers
      if (this.syncHandlers.has(key)) {
        this.syncHandlers.get(key).forEach(callback => {
          try {
            callback({ action: 'sync', payload: response.data, entityType, entityId });
          } catch (error) {
            console.error(`Error in sync handler for ${key}:`, error);
          }
        });
      }

      return response.data;
    } catch (error) {
      console.error(`Error syncing ${entityType}:${entityId}:`, error);
      throw error;
    }
  }
}

// Singleton instance
const realtimeSync = new RealtimeSync();

export default realtimeSync;
