/**
 *  System Enhancer
 *     
 * Universal Enhancement System for All Modules
 */

class SystemEnhancer {
  constructor() {
    this.enhancements = {
      performance: true,
      ui: true,
      analytics: true,
      security: true,
      accessibility: true,
      ai: false,
    };
    this.cache = new Map();
    this.metrics = {
      loadTime: 0,
      renderTime: 0,
      apiCalls: 0,
      cacheHits: 0,
      cacheMisses: 0,
    };
    this.init();
  }

  init() {
    this.setupPerformanceMonitoring();
    this.setupCaching();
    this.setupAnalytics();
    this.setupSecurity();
    this.setupAccessibility();
  }

  // ==================== Performance Enhancements ====================

  setupPerformanceMonitoring() {
    if (!this.enhancements.performance) return;

    // Monitor page load
    if (typeof window !== 'undefined' && window.performance) {
      window.addEventListener('load', () => {
        const perfData = window.performance.timing;
        this.metrics.loadTime = perfData.loadEventEnd - perfData.navigationStart;
        this.saveMetrics();
      });
    }

    // Monitor render time
    if (typeof performance !== 'undefined' && performance.mark) {
      performance.mark('render-start');
    }
  }

  setupCaching() {
    if (!this.enhancements.performance) return;

    // Setup cache with TTL
    this.cache = new Map();

    // Cleanup expired cache every 5 minutes
    setInterval(
      () => {
        this.cleanupCache();
      },
      5 * 60 * 1000
    );
  }

  getCached(key) {
    const cached = this.cache.get(key);
    if (cached && cached.expiry > Date.now()) {
      this.metrics.cacheHits++;
      return cached.data;
    }
    this.metrics.cacheMisses++;
    return null;
  }

  setCached(key, data, ttl = 5 * 60 * 1000) {
    this.cache.set(key, {
      data,
      expiry: Date.now() + ttl,
    });
  }

  cleanupCache() {
    const now = Date.now();
    for (const [key, value] of this.cache.entries()) {
      if (value.expiry <= now) {
        this.cache.delete(key);
      }
    }
  }

  // ==================== UI Enhancements ====================

  setupUIEnhancements() {
    if (!this.enhancements.ui) return;

    // Add smooth scrolling
    document.documentElement.style.scrollBehavior = 'smooth';

    // Add loading animations
    this.addLoadingAnimations();

    // Add hover effects
    this.addHoverEffects();

    // Add dark mode support
    this.setupDarkMode();
  }

  addLoadingAnimations() {
    const style = document.createElement('style');
    style.textContent = `
      @keyframes fadeIn {
        from { opacity: 0; transform: translateY(10px); }
        to { opacity: 1; transform: translateY(0); }
      }
      .enhanced-fade-in {
        animation: fadeIn 0.3s ease-in-out;
      }
    `;
    document.head.appendChild(style);
  }

  addHoverEffects() {
    const style = document.createElement('style');
    style.textContent = `
      .enhanced-hover {
        transition: all 0.3s ease;
      }
      .enhanced-hover:hover {
        transform: translateY(-2px);
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
      }
    `;
    document.head.appendChild(style);
  }

  setupDarkMode() {
    const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
    if (prefersDark) {
      document.body.classList.add('dark-mode');
    }
  }

  // ==================== Analytics Enhancements ====================

  setupAnalytics() {
    if (!this.enhancements.analytics) return;

    // Track user interactions
    document.addEventListener('click', e => {
      this.trackEvent('click', {
        element: e.target.tagName,
        id: e.target.id,
        class: e.target.className,
      });
    });

    // Track page views
    this.trackEvent('pageview', {
      path: window.location.pathname,
      timestamp: new Date().toISOString(),
    });
  }

  trackEvent(eventName, data) {
    // Store analytics data
    const analytics = JSON.parse(localStorage.getItem('analytics') || '[]');
    analytics.push({
      event: eventName,
      data,
      timestamp: new Date().toISOString(),
    });

    // Keep only last 1000 events
    if (analytics.length > 1000) {
      analytics.shift();
    }

    localStorage.setItem('analytics', JSON.stringify(analytics));
  }

  // ==================== Security Enhancements ====================

  setupSecurity() {
    if (!this.enhancements.security) return;

    // Add CSRF protection
    this.addCSRFProtection();

    // Add XSS protection
    this.addXSSProtection();

    // Add rate limiting
    this.addRateLimiting();
  }

  addCSRFProtection() {
    // Generate CSRF token
    const token = this.generateToken();
    localStorage.setItem('csrf_token', token);

    // Add token to all API requests
    if (window.apiClient && window.apiClient.interceptors) {
      window.apiClient.interceptors.request.use(config => {
        config.headers['X-CSRF-Token'] = token;
        return config;
      });
    }
  }

  addXSSProtection() {
    // Sanitize user input
    this.sanitizeInput = input => {
      const div = document.createElement('div');
      div.textContent = input;
      return div.innerHTML;
    };
  }

  addRateLimiting() {
    const requests = new Map();

    return (key, limit = 10, windowMs = 60000) => {
      const now = Date.now();
      const userRequests = requests.get(key) || [];

      // Remove old requests
      const validRequests = userRequests.filter(time => now - time < windowMs);

      if (validRequests.length >= limit) {
        return false; // Rate limit exceeded
      }

      validRequests.push(now);
      requests.set(key, validRequests);
      return true;
    };
  }

  generateToken() {
    return Array.from(crypto.getRandomValues(new Uint8Array(32)))
      .map(b => b.toString(16).padStart(2, '0'))
      .join('');
  }

  // ==================== Accessibility Enhancements ====================

  setupAccessibility() {
    if (!this.enhancements.accessibility) return;

    // Add keyboard navigation
    this.addKeyboardNavigation();

    // Add screen reader support
    this.addScreenReaderSupport();

    // Add high contrast mode
    this.addHighContrastMode();
  }

  addKeyboardNavigation() {
    document.addEventListener('keydown', e => {
      // Tab navigation
      if (e.key === 'Tab') {
        document.body.classList.add('keyboard-navigation');
      }
    });

    document.addEventListener('mousedown', () => {
      document.body.classList.remove('keyboard-navigation');
    });
  }

  addScreenReaderSupport() {
    // Add ARIA labels to interactive elements
    const interactiveElements = document.querySelectorAll('button, a, input, select, textarea');
    interactiveElements.forEach(el => {
      if (!el.getAttribute('aria-label') && !el.textContent.trim()) {
        el.setAttribute('aria-label', el.className || 'Interactive element');
      }
    });
  }

  addHighContrastMode() {
    const style = document.createElement('style');
    style.textContent = `
      .high-contrast {
        filter: contrast(1.2) brightness(1.1);
      }
      .high-contrast * {
        border-color: currentColor !important;
      }
    `;
    document.head.appendChild(style);
  }

  // ==================== AI Enhancements ====================

  setupAI() {
    if (!this.enhancements.ai) return;

    // Add smart suggestions
    this.addSmartSuggestions();

    // Add predictive analytics
    this.addPredictiveAnalytics();

    // Add natural language processing
    this.addNLP();
  }

  addSmartSuggestions() {
    // Analyze user behavior and provide suggestions
    const analytics = JSON.parse(localStorage.getItem('analytics') || '[]');
    const patterns = this.analyzePatterns(analytics);

    return patterns;
  }

  analyzePatterns(analytics) {
    // Simple pattern analysis
    const patterns = {
      mostUsedFeatures: [],
      timeOfDay: {},
      commonFlows: [],
    };

    // Analyze most used features
    const featureCounts = {};
    analytics.forEach(event => {
      if (event.data && event.data.id) {
        featureCounts[event.data.id] = (featureCounts[event.data.id] || 0) + 1;
      }
    });

    patterns.mostUsedFeatures = Object.entries(featureCounts)
      .sort((a, b) => b[1] - a[1])
      .slice(0, 5)
      .map(([feature]) => feature);

    return patterns;
  }

  addPredictiveAnalytics() {
    // Predict user needs based on patterns
    return {
      predictNextAction: currentContext => {
        // Simple prediction based on context
        return 'suggested_action';
      },
    };
  }

  addNLP() {
    // Natural language processing for search and commands
    return {
      parseQuery: query => {
        // Simple NLP parsing
        return {
          intent: 'search',
          entities: query.split(' '),
        };
      },
    };
  }

  // ==================== Utility Methods ====================

  saveMetrics() {
    localStorage.setItem('system_metrics', JSON.stringify(this.metrics));
  }

  loadMetrics() {
    const stored = localStorage.getItem('system_metrics');
    if (stored) {
      try {
        this.metrics = { ...this.metrics, ...JSON.parse(stored) };
      } catch (error) {
        console.error('Error loading metrics:', error);
      }
    }
  }

  getMetrics() {
    return {
      ...this.metrics,
      cacheSize: this.cache.size,
      cacheHitRate:
        this.metrics.cacheHits / (this.metrics.cacheHits + this.metrics.cacheMisses) || 0,
    };
  }

  // ==================== Module Enhancement ====================

  enhanceModule(moduleInstance) {
    // Add performance monitoring
    if (this.enhancements.performance) {
      const originalRender = moduleInstance.render;
      if (originalRender) {
        moduleInstance.render = (...args) => {
          const start = performance.now();
          const result = originalRender.apply(moduleInstance, args);
          const end = performance.now();
          this.metrics.renderTime = end - start;
          return result;
        };
      }
    }

    // Add caching to API calls
    if (this.enhancements.performance && moduleInstance.loadData) {
      const originalLoadData = moduleInstance.loadData;
      moduleInstance.loadData = async (...args) => {
        const cacheKey = `data_${moduleInstance.constructor.name}_${JSON.stringify(args)}`;
        const cached = this.getCached(cacheKey);
        if (cached) {
          return cached;
        }

        const data = await originalLoadData.apply(moduleInstance, args);
        this.setCached(cacheKey, data);
        return data;
      };
    }

    // Add analytics tracking
    if (this.enhancements.analytics) {
      const originalMethod = moduleInstance.render;
      if (originalMethod) {
        moduleInstance.render = (...args) => {
          this.trackEvent('module_render', {
            module: moduleInstance.constructor.name,
            timestamp: new Date().toISOString(),
          });
          return originalMethod.apply(moduleInstance, args);
        };
      }
    }

    return moduleInstance;
  }
}

// Export singleton instance
const systemEnhancer = new SystemEnhancer();

// Auto-enhance all modules
if (typeof window !== 'undefined') {
  window.systemEnhancer = systemEnhancer;

  // Enhance modules on load
  document.addEventListener('DOMContentLoaded', () => {
    systemEnhancer.setupUIEnhancements();
    systemEnhancer.setupAccessibility();
  });
}

export default systemEnhancer;
