/**
 *  Dark Mode Utility
 *   
 *     
 */

class DarkModeManager {
  constructor() {
    this.isDarkMode = false;
    this.theme = 'light';
    this.preferences = {
      autoDetect: true,
      rememberPreference: true,
      transition: true,
    };
    this.init();
  }

  /**
   * Initialize dark mode manager
   */
  init() {
    // Load saved preference
    if (this.preferences.rememberPreference) {
      const savedTheme = localStorage.getItem('theme');
      if (savedTheme) {
        this.theme = savedTheme;
        this.isDarkMode = savedTheme === 'dark';
      } else if (this.preferences.autoDetect) {
        // Auto-detect system preference
        this.autoDetectTheme();
      }
    } else if (this.preferences.autoDetect) {
      this.autoDetectTheme();
    }

    // Apply theme
    this.applyTheme();

    // Listen for system theme changes
    if (this.preferences.autoDetect && window.matchMedia) {
      const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
      mediaQuery.addEventListener('change', e => {
        if (this.preferences.autoDetect && !localStorage.getItem('theme')) {
          this.setTheme(e.matches ? 'dark' : 'light', false);
        }
      });
    }
  }

  /**
   * Auto-detect system theme preference
   */
  autoDetectTheme() {
    if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
      this.theme = 'dark';
      this.isDarkMode = true;
    } else {
      this.theme = 'light';
      this.isDarkMode = false;
    }
  }

  /**
   * Toggle dark mode
   */
  toggle() {
    this.setTheme(this.isDarkMode ? 'light' : 'dark');
  }

  /**
   * Set theme
   */
  setTheme(theme, savePreference = true) {
    this.theme = theme;
    this.isDarkMode = theme === 'dark';

    if (savePreference && this.preferences.rememberPreference) {
      localStorage.setItem('theme', theme);
    }

    this.applyTheme();

    // Emit theme change event
    this.emitThemeChange();
  }

  /**
   * Apply theme to document
   */
  applyTheme() {
    const html = document.documentElement;
    const body = document.body;

    if (this.preferences.transition) {
      html.style.transition = 'background-color 0.3s ease, color 0.3s ease';
    }

    if (this.isDarkMode) {
      html.classList.add('dark-mode');
      html.classList.remove('light-mode');
      html.setAttribute('data-theme', 'dark');
      body.classList.add('dark-mode');
      body.classList.remove('light-mode');
    } else {
      html.classList.add('light-mode');
      html.classList.remove('dark-mode');
      html.setAttribute('data-theme', 'light');
      body.classList.add('light-mode');
      body.classList.remove('dark-mode');
    }

    // Update CSS variables
    this.updateCSSVariables();
  }

  /**
   * Update CSS variables for theme
   */
  updateCSSVariables() {
    const root = document.documentElement;

    if (this.isDarkMode) {
      // Dark mode colors
      root.style.setProperty('--bg-primary', '#1a1a1a');
      root.style.setProperty('--bg-secondary', '#2d2d2d');
      root.style.setProperty('--bg-tertiary', '#3d3d3d');
      root.style.setProperty('--text-primary', '#ffffff');
      root.style.setProperty('--text-secondary', '#b0b0b0');
      root.style.setProperty('--text-tertiary', '#808080');
      root.style.setProperty('--border-color', '#404040');
      root.style.setProperty('--shadow-color', 'rgba(0, 0, 0, 0.3)');
      root.style.setProperty('--accent-color', '#667eea');
      root.style.setProperty('--accent-hover', '#764ba2');
    } else {
      // Light mode colors
      root.style.setProperty('--bg-primary', '#ffffff');
      root.style.setProperty('--bg-secondary', '#f5f7fa');
      root.style.setProperty('--bg-tertiary', '#e9ecef');
      root.style.setProperty('--text-primary', '#2c3e50');
      root.style.setProperty('--text-secondary', '#7f8c8d');
      root.style.setProperty('--text-tertiary', '#95a5a6');
      root.style.setProperty('--border-color', '#e9ecef');
      root.style.setProperty('--shadow-color', 'rgba(0, 0, 0, 0.1)');
      root.style.setProperty('--accent-color', '#667eea');
      root.style.setProperty('--accent-hover', '#764ba2');
    }
  }

  /**
   * Emit theme change event
   */
  emitThemeChange() {
    const event = new CustomEvent('themechange', {
      detail: {
        theme: this.theme,
        isDarkMode: this.isDarkMode,
      },
    });
    window.dispatchEvent(event);
  }

  /**
   * Get current theme
   */
  getTheme() {
    return {
      theme: this.theme,
      isDarkMode: this.isDarkMode,
    };
  }

  /**
   * Set preferences
   */
  setPreferences(preferences) {
    this.preferences = {
      ...this.preferences,
      ...preferences,
    };
  }

  /**
   * Get preferences
   */
  getPreferences() {
    return { ...this.preferences };
  }
}

// Export singleton instance
const darkModeManager = new DarkModeManager();

// Make available globally
if (typeof window !== 'undefined') {
  window.darkModeManager = darkModeManager;

  // Add toggle function
  window.toggleDarkMode = () => darkModeManager.toggle();
}

export default darkModeManager;
