/**
 *  Route Lazy Loader
 *   
 *      
 */

import lazyLoader from './lazy-loader.js';

class RouteLazyLoader {
  constructor() {
    this.routeModules = new Map();
    this.loadedRoutes = new Set();
    this.routeConfig = new Map();
    this.init();
  }

  /**
   * Initialize route lazy loader
   */
  init() {
    // Register all routes with lazy loading
    this.registerRoutes();
  }

  /**
   * Register routes with lazy loading
   */
  registerRoutes() {
    // Advanced Systems Routes
    this.registerRoute(
      'advanced-assessments',
      () => import('../rehabilitation-center-advanced-assessments.js')
    );
    this.registerRoute('advanced-hr', () => import('../rehabilitation-center-advanced-hr.js'));
    this.registerRoute(
      'advanced-budget',
      () => import('../rehabilitation-center-advanced-budget.js')
    );
    this.registerRoute(
      'advanced-training',
      () => import('../rehabilitation-center-advanced-training.js')
    );
    this.registerRoute(
      'advanced-certifications',
      () => import('../rehabilitation-center-advanced-certifications.js')
    );
    this.registerRoute(
      'advanced-communication',
      () => import('../rehabilitation-center-advanced-communication.js')
    );
    this.registerRoute(
      'advanced-reports',
      () => import('../rehabilitation-center-advanced-reports.js')
    );
    this.registerRoute(
      'advanced-security',
      () => import('../rehabilitation-center-advanced-security.js')
    );
    this.registerRoute(
      'advanced-integration',
      () => import('../rehabilitation-center-advanced-integration.js')
    );
  }

  /**
   * Register route with lazy loading
   */
  registerRoute(routeName, loaderFunction, options = {}) {
    this.routeConfig.set(routeName, {
      loader: loaderFunction,
      preload: options.preload || false,
      priority: options.priority || 'normal',
    });
  }

  /**
   * Load route module
   */
  async loadRoute(routeName) {
    // Check if already loaded
    if (this.loadedRoutes.has(routeName)) {
      return this.routeModules.get(routeName);
    }

    // Check if route is registered
    const routeConfig = this.routeConfig.get(routeName);
    if (!routeConfig) {
      throw new Error(`Route not found: ${routeName}`);
    }

    try {
      // Load route module
      const module = await routeConfig.loader();
      const routeModule = module.default || module;

      // Cache loaded module
      this.routeModules.set(routeName, routeModule);
      this.loadedRoutes.add(routeName);

      return routeModule;
    } catch (error) {
      console.error(`Error loading route: ${routeName}`, error);
      throw error;
    }
  }

  /**
   * Preload route (load in background)
   */
  async preloadRoute(routeName) {
    const routeConfig = this.routeConfig.get(routeName);
    if (!routeConfig) return;

    if (routeConfig.preload) {
      this.loadRoute(routeName).catch(error => {
        console.warn(`Preload failed for route: ${routeName}`, error);
      });
    }
  }

  /**
   * Preload multiple routes
   */
  async preloadRoutes(routeNames) {
    const promises = routeNames.map(name => this.preloadRoute(name));
    await Promise.allSettled(promises);
  }

  /**
   * Preload high priority routes
   */
  async preloadHighPriorityRoutes() {
    const highPriorityRoutes = Array.from(this.routeConfig.entries())
      .filter(([name, config]) => config.priority === 'high')
      .map(([name]) => name);

    await this.preloadRoutes(highPriorityRoutes);
  }

  /**
   * Get route loading status
   */
  getRouteStatus(routeName) {
    return {
      registered: this.routeConfig.has(routeName),
      loaded: this.loadedRoutes.has(routeName),
      cached: this.routeModules.has(routeName),
    };
  }

  /**
   * Get all routes status
   */
  getAllRoutesStatus() {
    const status = {};
    this.routeConfig.forEach((config, routeName) => {
      status[routeName] = this.getRouteStatus(routeName);
    });
    return status;
  }

  /**
   * Clear route cache
   */
  clearRouteCache(routeName = null) {
    if (routeName) {
      this.loadedRoutes.delete(routeName);
      this.routeModules.delete(routeName);
    } else {
      this.loadedRoutes.clear();
      this.routeModules.clear();
    }
  }
}

// Export singleton instance
const routeLazyLoader = new RouteLazyLoader();

// Make available globally
if (typeof window !== 'undefined') {
  window.routeLazyLoader = routeLazyLoader;
}

export default routeLazyLoader;
