/**
 *  Advanced Caching System
 *      
 */

class AdvancedCache {
  constructor() {
    this.cache = new Map();
    this.stats = {
      hits: 0,
      misses: 0,
      sets: 0,
      deletes: 0,
      size: 0,
    };
    this.maxSize = 1000; // Maximum number of cache entries
    this.defaultTTL = 5 * 60 * 1000; // 5 minutes
    this.init();
  }

  init() {
    // Load cache from localStorage on init
    this.loadFromStorage();

    // Cleanup expired entries every minute
    setInterval(() => {
      this.cleanup();
    }, 60 * 1000);

    // Save cache to localStorage periodically
    setInterval(() => {
      this.saveToStorage();
    }, 30 * 1000);
  }

  // ==================== Basic Operations ====================

  get(key) {
    const entry = this.cache.get(key);

    if (!entry) {
      this.stats.misses++;
      return null;
    }

    // Check if expired
    if (entry.expiry && entry.expiry < Date.now()) {
      this.cache.delete(key);
      this.stats.misses++;
      return null;
    }

    // Update access time
    entry.lastAccessed = Date.now();
    this.stats.hits++;

    return entry.data;
  }

  set(key, data, ttl = this.defaultTTL) {
    // Check cache size limit
    if (this.cache.size >= this.maxSize && !this.cache.has(key)) {
      this.evictLRU();
    }

    const entry = {
      data,
      createdAt: Date.now(),
      lastAccessed: Date.now(),
      expiry: ttl ? Date.now() + ttl : null,
      accessCount: 0,
    };

    this.cache.set(key, entry);
    this.stats.sets++;
    this.stats.size = this.cache.size;

    return true;
  }

  delete(key) {
    const deleted = this.cache.delete(key);
    if (deleted) {
      this.stats.deletes++;
      this.stats.size = this.cache.size;
    }
    return deleted;
  }

  clear() {
    this.cache.clear();
    this.stats.size = 0;
  }

  // ==================== Advanced Operations ====================

  getOrSet(key, fetchFn, ttl = this.defaultTTL) {
    const cached = this.get(key);
    if (cached !== null) {
      return Promise.resolve(cached);
    }

    return fetchFn().then(data => {
      this.set(key, data, ttl);
      return data;
    });
  }

  async getOrSetAsync(key, fetchFn, ttl = this.defaultTTL) {
    const cached = this.get(key);
    if (cached !== null) {
      return cached;
    }

    const data = await fetchFn();
    this.set(key, data, ttl);
    return data;
  }

  // ==================== Cache Management ====================

  evictLRU() {
    // Evict least recently used entry
    let lruKey = null;
    let lruTime = Infinity;

    for (const [key, entry] of this.cache.entries()) {
      if (entry.lastAccessed < lruTime) {
        lruTime = entry.lastAccessed;
        lruKey = key;
      }
    }

    if (lruKey) {
      this.cache.delete(lruKey);
    }
  }

  cleanup() {
    const now = Date.now();
    let cleaned = 0;

    for (const [key, entry] of this.cache.entries()) {
      if (entry.expiry && entry.expiry < now) {
        this.cache.delete(key);
        cleaned++;
      }
    }

    this.stats.size = this.cache.size;
    return cleaned;
  }

  // ==================== Cache Patterns ====================

  // Cache with tags for easy invalidation
  setWithTags(key, data, tags = [], ttl = this.defaultTTL) {
    const entry = {
      data,
      tags,
      createdAt: Date.now(),
      lastAccessed: Date.now(),
      expiry: ttl ? Date.now() + ttl : null,
    };

    this.cache.set(key, entry);
    this.stats.sets++;
    this.stats.size = this.cache.size;
  }

  invalidateByTag(tag) {
    let invalidated = 0;

    for (const [key, entry] of this.cache.entries()) {
      if (entry.tags && entry.tags.includes(tag)) {
        this.cache.delete(key);
        invalidated++;
      }
    }

    this.stats.size = this.cache.size;
    return invalidated;
  }

  // Cache with dependencies
  setWithDependencies(key, data, dependencies = [], ttl = this.defaultTTL) {
    const entry = {
      data,
      dependencies,
      createdAt: Date.now(),
      lastAccessed: Date.now(),
      expiry: ttl ? Date.now() + ttl : null,
    };

    this.cache.set(key, entry);
    this.stats.sets++;
    this.stats.size = this.cache.size;
  }

  invalidateDependencies(key) {
    let invalidated = 0;

    for (const [cacheKey, entry] of this.cache.entries()) {
      if (entry.dependencies && entry.dependencies.includes(key)) {
        this.cache.delete(cacheKey);
        invalidated++;
      }
    }

    this.stats.size = this.cache.size;
    return invalidated;
  }

  // ==================== Statistics ====================

  getStats() {
    const total = this.stats.hits + this.stats.misses;
    const hitRate = total > 0 ? (this.stats.hits / total) * 100 : 0;

    return {
      ...this.stats,
      hitRate: hitRate.toFixed(2) + '%',
      totalRequests: total,
    };
  }

  resetStats() {
    this.stats = {
      hits: 0,
      misses: 0,
      sets: 0,
      deletes: 0,
      size: this.cache.size,
    };
  }

  // ==================== Persistence ====================

  saveToStorage() {
    try {
      const cacheData = Array.from(this.cache.entries()).map(([key, entry]) => ({
        key,
        data: entry.data,
        createdAt: entry.createdAt,
        expiry: entry.expiry,
        tags: entry.tags || [],
        dependencies: entry.dependencies || [],
      }));

      // Only save non-expired entries
      const now = Date.now();
      const validEntries = cacheData.filter(entry => !entry.expiry || entry.expiry > now);

      localStorage.setItem(
        'advanced_cache',
        JSON.stringify({
          entries: validEntries,
          stats: this.stats,
          savedAt: Date.now(),
        })
      );
    } catch (error) {
      console.error('Error saving cache to storage:', error);
    }
  }

  loadFromStorage() {
    try {
      const stored = localStorage.getItem('advanced_cache');
      if (!stored) return;

      const cacheData = JSON.parse(stored);
      const now = Date.now();

      // Only load non-expired entries
      cacheData.entries.forEach(entry => {
        if (!entry.expiry || entry.expiry > now) {
          this.cache.set(entry.key, {
            data: entry.data,
            createdAt: entry.createdAt,
            lastAccessed: entry.createdAt,
            expiry: entry.expiry,
            tags: entry.tags || [],
            dependencies: entry.dependencies || [],
          });
        }
      });

      if (cacheData.stats) {
        this.stats = { ...this.stats, ...cacheData.stats };
      }

      this.stats.size = this.cache.size;
    } catch (error) {
      console.error('Error loading cache from storage:', error);
    }
  }

  // ==================== Cache Warming ====================

  warmCache(keys, fetchFn) {
    // Pre-load cache with data
    return Promise.all(keys.map(key => this.getOrSet(key, () => fetchFn(key))));
  }

  // ==================== Cache Invalidation Strategies ====================

  invalidatePattern(pattern) {
    // Invalidate entries matching a pattern
    const regex = new RegExp(pattern);
    let invalidated = 0;

    for (const key of this.cache.keys()) {
      if (regex.test(key)) {
        this.cache.delete(key);
        invalidated++;
      }
    }

    this.stats.size = this.cache.size;
    return invalidated;
  }

  invalidateOlderThan(ageMs) {
    // Invalidate entries older than specified age
    const now = Date.now();
    let invalidated = 0;

    for (const [key, entry] of this.cache.entries()) {
      if (entry.createdAt && now - entry.createdAt > ageMs) {
        this.cache.delete(key);
        invalidated++;
      }
    }

    this.stats.size = this.cache.size;
    return invalidated;
  }
}

// Export singleton instance
const advancedCache = new AdvancedCache();

if (typeof window !== 'undefined') {
  window.advancedCache = advancedCache;
}

export default advancedCache;
