/**
 *  AI Assistant
 *    
 * Intelligent Assistant for All Systems
 */

class AIAssistant {
  constructor() {
    this.context = {
      currentModule: null,
      userPreferences: {},
      history: [],
    };
    this.capabilities = {
      suggestions: true,
      predictions: true,
      automation: true,
      analysis: true,
    };
    this.init();
  }

  init() {
    this.loadContext();
    this.setupEventListeners();
  }

  loadContext() {
    const stored = localStorage.getItem('ai_assistant_context');
    if (stored) {
      try {
        this.context = { ...this.context, ...JSON.parse(stored) };
      } catch (error) {
        console.error('Error loading AI context:', error);
      }
    }
  }

  saveContext() {
    localStorage.setItem('ai_assistant_context', JSON.stringify(this.context));
  }

  setupEventListeners() {
    // Listen for module changes
    document.addEventListener('moduleChange', e => {
      this.context.currentModule = e.detail.module;
      this.saveContext();
    });
  }

  // ==================== Smart Suggestions ====================

  getSuggestions(context) {
    const suggestions = [];

    // Context-based suggestions
    if (context.currentModule === 'patients') {
      suggestions.push({
        type: 'action',
        title: '  ',
        description: '    ',
        action: () => {
          if (window.patientsSystem) {
            window.patientsSystem.showCreatePatientModal();
          }
        },
        priority: 'high',
      });
    }

    // Pattern-based suggestions
    const patterns = this.analyzePatterns();
    if (patterns.frequentAction) {
      suggestions.push({
        type: 'shortcut',
        title: ' ',
        description: ` ${patterns.frequentAction} `,
        action: patterns.frequentAction,
        priority: 'medium',
      });
    }

    return suggestions;
  }

  analyzePatterns() {
    const analytics = JSON.parse(localStorage.getItem('analytics') || '[]');
    const recentActions = analytics.slice(-50);

    // Find most frequent action
    const actionCounts = {};
    recentActions.forEach(event => {
      if (event.data && event.data.action) {
        actionCounts[event.data.action] = (actionCounts[event.data.action] || 0) + 1;
      }
    });

    const mostFrequent = Object.entries(actionCounts).sort((a, b) => b[1] - a[1])[0];

    return {
      frequentAction: mostFrequent ? mostFrequent[0] : null,
      frequency: mostFrequent ? mostFrequent[1] : 0,
    };
  }

  // ==================== Predictions ====================

  predictNextAction(context) {
    const predictions = [];

    // Time-based predictions
    const hour = new Date().getHours();
    if (hour >= 9 && hour <= 12) {
      predictions.push({
        action: 'morning_routine',
        probability: 0.8,
        description: ' :   ',
      });
    }

    // Pattern-based predictions
    const patterns = this.analyzePatterns();
    if (patterns.frequentAction) {
      predictions.push({
        action: patterns.frequentAction,
        probability: 0.6,
        description: `    ${patterns.frequentAction}`,
      });
    }

    return predictions;
  }

  // ==================== Automation ====================

  automateTask(task, context) {
    switch (task.type) {
      case 'data_entry':
        return this.automateDataEntry(task, context);
      case 'report_generation':
        return this.automateReportGeneration(task, context);
      case 'notification':
        return this.automateNotification(task, context);
      default:
        return { success: false, error: 'Unknown task type' };
    }
  }

  automateDataEntry(task, context) {
    // Auto-fill forms based on patterns
    const patterns = this.analyzePatterns();

    return {
      success: true,
      data: {
        suggestions: patterns.frequentAction ? [patterns.frequentAction] : [],
      },
    };
  }

  automateReportGeneration(task, context) {
    // Generate reports automatically
    return {
      success: true,
      report: {
        type: task.reportType,
        generatedAt: new Date().toISOString(),
        data: [],
      },
    };
  }

  automateNotification(task, context) {
    // Send automated notifications
    return {
      success: true,
      notification: {
        type: task.notificationType,
        sentAt: new Date().toISOString(),
      },
    };
  }

  // ==================== Analysis ====================

  analyzeData(data, type) {
    switch (type) {
      case 'trend':
        return this.analyzeTrends(data);
      case 'anomaly':
        return this.detectAnomalies(data);
      case 'correlation':
        return this.findCorrelations(data);
      default:
        return { success: false, error: 'Unknown analysis type' };
    }
  }

  analyzeTrends(data) {
    // Simple trend analysis
    if (!data || data.length < 2) {
      return { trend: 'insufficient_data' };
    }

    const values = data.map(d => d.value || 0);
    const firstHalf = values.slice(0, Math.floor(values.length / 2));
    const secondHalf = values.slice(Math.floor(values.length / 2));

    const firstAvg = firstHalf.reduce((a, b) => a + b, 0) / firstHalf.length;
    const secondAvg = secondHalf.reduce((a, b) => a + b, 0) / secondHalf.length;

    if (secondAvg > firstAvg * 1.1) {
      return { trend: 'increasing', rate: ((secondAvg - firstAvg) / firstAvg) * 100 };
    } else if (secondAvg < firstAvg * 0.9) {
      return { trend: 'decreasing', rate: ((firstAvg - secondAvg) / firstAvg) * 100 };
    } else {
      return { trend: 'stable' };
    }
  }

  detectAnomalies(data) {
    // Simple anomaly detection
    if (!data || data.length < 3) {
      return { anomalies: [] };
    }

    const values = data.map(d => d.value || 0);
    const mean = values.reduce((a, b) => a + b, 0) / values.length;
    const variance = values.reduce((sum, val) => sum + Math.pow(val - mean, 2), 0) / values.length;
    const stdDev = Math.sqrt(variance);

    const anomalies = [];
    values.forEach((value, index) => {
      if (Math.abs(value - mean) > 2 * stdDev) {
        anomalies.push({
          index,
          value,
          deviation: value - mean,
        });
      }
    });

    return { anomalies };
  }

  findCorrelations(data) {
    // Simple correlation analysis
    if (!data || data.length < 2) {
      return { correlations: [] };
    }

    // Placeholder for correlation analysis
    return {
      correlations: [
        {
          variable1: 'variable_a',
          variable2: 'variable_b',
          correlation: 0.75,
          strength: 'strong',
        },
      ],
    };
  }

  // ==================== Natural Language Processing ====================

  processQuery(query) {
    // Simple NLP processing
    const intents = {
      search: ['', '', 'find', 'search'],
      create: ['', '', '', 'create', 'add', 'new'],
      update: ['', '', 'update', 'edit', 'modify'],
      delete: ['', '', 'delete', 'remove'],
      report: ['', 'report', 'summary'],
    };

    const queryLower = query.toLowerCase();
    let detectedIntent = 'unknown';
    let entities = [];

    for (const [intent, keywords] of Object.entries(intents)) {
      if (keywords.some(keyword => queryLower.includes(keyword))) {
        detectedIntent = intent;
        break;
      }
    }

    // Extract entities (simple extraction)
    const words = query.split(' ');
    entities = words.filter(word => word.length > 3);

    return {
      intent: detectedIntent,
      entities,
      confidence: 0.7,
    };
  }

  // ==================== Learning ====================

  learnFromInteraction(interaction) {
    // Store interaction for learning
    this.context.history.push({
      ...interaction,
      timestamp: new Date().toISOString(),
    });

    // Keep only last 1000 interactions
    if (this.context.history.length > 1000) {
      this.context.history.shift();
    }

    this.saveContext();
  }

  getRecommendations() {
    const recommendations = [];

    // Based on history
    const recentInteractions = this.context.history.slice(-20);
    const commonPatterns = this.findCommonPatterns(recentInteractions);

    commonPatterns.forEach(pattern => {
      recommendations.push({
        type: 'pattern',
        title: pattern.title,
        description: pattern.description,
        action: pattern.action,
      });
    });

    return recommendations;
  }

  findCommonPatterns(interactions) {
    // Simple pattern finding
    const patterns = [];

    // Find most common module
    const moduleCounts = {};
    interactions.forEach(interaction => {
      if (interaction.module) {
        moduleCounts[interaction.module] = (moduleCounts[interaction.module] || 0) + 1;
      }
    });

    const mostCommonModule = Object.entries(moduleCounts).sort((a, b) => b[1] - a[1])[0];

    if (mostCommonModule && mostCommonModule[1] > 5) {
      patterns.push({
        title: ` : ${mostCommonModule[0]}`,
        description: `  ${mostCommonModule[0]}  `,
        action: `focus_on_${mostCommonModule[0]}`,
      });
    }

    return patterns;
  }
}

// Export singleton instance
const aiAssistant = new AIAssistant();

if (typeof window !== 'undefined') {
  window.aiAssistant = aiAssistant;
}

export default aiAssistant;
