/**
 *  Connection Manager
 *    Backend  
 */

import { getApiBaseUrl, getWebSocketUrl } from '../config/environment.js';
import realtimeClient from './realtime-client.js';

class ConnectionManager {
  constructor() {
    this.apiBaseUrl = getApiBaseUrl();
    this.wsUrl = getWebSocketUrl();
    this.isOnline = navigator.onLine;
    this.apiConnected = false;
    this.wsConnected = false;
    this.lastHealthCheck = null;
    this.healthCheckInterval = null;
    this.listeners = new Map();

    this.init();
  }

  /**
   * Initialize connection manager
   */
  init() {
    // Monitor online/offline status
    window.addEventListener('online', () => this.handleOnline());
    window.addEventListener('offline', () => this.handleOffline());

    // Start health checks
    this.startHealthChecks();

    // Connect WebSocket
    this.connectWebSocket();

    // Initial health check
    this.checkHealth();
  }

  /**
   * Handle online event
   */
  handleOnline() {
    this.isOnline = true;
    this.emit('online');
    this.checkHealth();
    this.connectWebSocket();
  }

  /**
   * Handle offline event
   */
  handleOffline() {
    this.isOnline = false;
    this.apiConnected = false;
    this.wsConnected = false;
    this.emit('offline');
  }

  /**
   * Start periodic health checks
   */
  startHealthChecks() {
    // Check every 30 seconds
    this.healthCheckInterval = setInterval(() => {
      this.checkHealth();
    }, 30000);
  }

  /**
   * Stop health checks
   */
  stopHealthChecks() {
    if (this.healthCheckInterval) {
      clearInterval(this.healthCheckInterval);
      this.healthCheckInterval = null;
    }
  }

  /**
   * Check API health
   */
  async checkHealth() {
    if (!this.isOnline) {
      this.apiConnected = false;
      return;
    }

    try {
      const response = await fetch(`${this.apiBaseUrl.replace('/api', '')}/healthz`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
        signal: AbortSignal.timeout(5000), // 5 second timeout
      });

      if (response.ok) {
        const data = await response.json();
        this.apiConnected = true;
        this.lastHealthCheck = {
          timestamp: new Date(),
          status: 'healthy',
          data,
        };
        this.emit('health-check', { status: 'healthy', data });
      } else {
        this.apiConnected = false;
        this.emit('health-check', { status: 'unhealthy', error: 'Bad response' });
      }
    } catch (error) {
      this.apiConnected = false;
      this.lastHealthCheck = {
        timestamp: new Date(),
        status: 'error',
        error: error.message,
      };
      this.emit('health-check', { status: 'error', error: error.message });
    }
  }

  /**
   * Connect WebSocket
   */
  connectWebSocket() {
    if (!this.isOnline) return;

    // Listen to WebSocket events
    realtimeClient.on('connected', () => {
      this.wsConnected = true;
      this.emit('ws-connected');
    });

    realtimeClient.on('disconnected', () => {
      this.wsConnected = false;
      this.emit('ws-disconnected');
    });

    realtimeClient.on('error', error => {
      this.wsConnected = false;
      this.emit('ws-error', error);
    });

    // Connect
    realtimeClient.connect(this.wsUrl);
  }

  /**
   * Disconnect WebSocket
   */
  disconnectWebSocket() {
    realtimeClient.disconnect();
    this.wsConnected = false;
  }

  /**
   * Get connection status
   */
  getStatus() {
    return {
      isOnline: this.isOnline,
      apiConnected: this.apiConnected,
      wsConnected: this.wsConnected,
      lastHealthCheck: this.lastHealthCheck,
      apiBaseUrl: this.apiBaseUrl,
      wsUrl: this.wsUrl,
    };
  }

  /**
   * Check if fully connected
   */
  isFullyConnected() {
    return this.isOnline && this.apiConnected;
  }

  /**
   * Listen to events
   */
  on(event, callback) {
    if (!this.listeners.has(event)) {
      this.listeners.set(event, []);
    }
    this.listeners.get(event).push(callback);
  }

  /**
   * Remove event listener
   */
  off(event, callback) {
    if (this.listeners.has(event)) {
      const callbacks = this.listeners.get(event);
      const index = callbacks.indexOf(callback);
      if (index > -1) {
        callbacks.splice(index, 1);
      }
    }
  }

  /**
   * Emit event
   */
  emit(event, data) {
    if (this.listeners.has(event)) {
      this.listeners.get(event).forEach(callback => {
        try {
          callback(data);
        } catch (error) {
          console.error(`Error in connection manager listener for ${event}:`, error);
        }
      });
    }
  }

  /**
   * Cleanup
   */
  destroy() {
    this.stopHealthChecks();
    this.disconnectWebSocket();
    this.listeners.clear();
  }
}

// Singleton instance
const connectionManager = new ConnectionManager();

// Export
export default connectionManager;
