/**
 *  Real-time Client
 *  WebSocket  
 */

import { getWebSocketUrl } from '../config/environment.js';

class RealtimeClient {
  constructor() {
    this.socket = null;
    this.isConnected = false;
    this.reconnectAttempts = 0;
    this.maxReconnectAttempts = 5;
    this.reconnectDelay = 1000;
    this.listeners = new Map();
  }

  /**
   * Connect to WebSocket server
   */
  connect(url = null) {
    const wsUrl = url || getWebSocketUrl();

    try {
      this.socket = new WebSocket(wsUrl);

      this.socket.onopen = () => {
        console.log(' WebSocket connected');
        this.isConnected = true;
        this.reconnectAttempts = 0;
        this.emit('connected');

        // Authenticate if token exists
        const token = localStorage.getItem('adminToken');
        const user = JSON.parse(localStorage.getItem('adminUser') || '{}');
        if (token && user.id) {
          this.authenticate(user.id, token);
        }
      };

      this.socket.onmessage = event => {
        try {
          const data = JSON.parse(event.data);
          this.handleMessage(data);
        } catch (error) {
          console.error('Error parsing WebSocket message:', error);
        }
      };

      this.socket.onerror = error => {
        console.error('WebSocket error:', error);
        this.emit('error', error);
      };

      this.socket.onclose = () => {
        console.log(' WebSocket disconnected');
        this.isConnected = false;
        this.emit('disconnected');
        this.attemptReconnect();
      };
    } catch (error) {
      console.error('Error connecting to WebSocket:', error);
      this.attemptReconnect();
    }
  }

  /**
   * Attempt to reconnect
   */
  attemptReconnect() {
    if (this.reconnectAttempts >= this.maxReconnectAttempts) {
      console.error('Max reconnection attempts reached');
      return;
    }

    this.reconnectAttempts++;
    const delay = this.reconnectDelay * Math.pow(2, this.reconnectAttempts - 1);

    console.log(`Reconnecting in ${delay}ms (attempt ${this.reconnectAttempts})`);

    setTimeout(() => {
      this.connect();
    }, delay);
  }

  /**
   * Authenticate with server
   */
  authenticate(userId, token) {
    if (this.isConnected) {
      this.send('authenticate', { userId, token });
    }
  }

  /**
   * Join a room
   */
  joinRoom(roomId) {
    if (this.isConnected) {
      this.send('join-room', roomId);
    }
  }

  /**
   * Leave a room
   */
  leaveRoom(roomId) {
    if (this.isConnected) {
      this.send('leave-room', roomId);
    }
  }

  /**
   * Send message
   */
  send(event, data) {
    if (this.isConnected && this.socket) {
      this.socket.send(JSON.stringify({ event, data }));
    } else {
      console.warn('WebSocket not connected, message not sent:', event);
    }
  }

  /**
   * Handle incoming message
   */
  handleMessage(data) {
    const { event, data: eventData } = data;

    // Emit to listeners
    if (this.listeners.has(event)) {
      this.listeners.get(event).forEach(callback => {
        try {
          callback(eventData);
        } catch (error) {
          console.error(`Error in listener for ${event}:`, error);
        }
      });
    }
  }

  /**
   * Listen to event
   */
  on(event, callback) {
    if (!this.listeners.has(event)) {
      this.listeners.set(event, []);
    }
    this.listeners.get(event).push(callback);
  }

  /**
   * Remove event listener
   */
  off(event, callback) {
    if (this.listeners.has(event)) {
      const callbacks = this.listeners.get(event);
      const index = callbacks.indexOf(callback);
      if (index > -1) {
        callbacks.splice(index, 1);
      }
    }
  }

  /**
   * Emit event to local listeners
   */
  emit(event, data) {
    if (this.listeners.has(event)) {
      this.listeners.get(event).forEach(callback => {
        try {
          callback(data);
        } catch (error) {
          console.error(`Error in emit listener for ${event}:`, error);
        }
      });
    }
  }

  /**
   * Disconnect
   */
  disconnect() {
    if (this.socket) {
      this.socket.close();
      this.socket = null;
      this.isConnected = false;
    }
  }
}

// Singleton instance
const realtimeClient = new RealtimeClient();

// Auto-connect on load (if in browser)
if (typeof window !== 'undefined') {
  window.addEventListener('load', () => {
    realtimeClient.connect();
  });
}

export default realtimeClient;
