/**
 *  Dark Mode Toggle Component
 *    
 */

import darkModeManager from '../utils/dark-mode.js';

class DarkModeToggle {
  constructor(container) {
    this.container = container;
    this.init();
  }

  /**
   * Initialize dark mode toggle
   */
  init() {
    this.render();
    this.setupEventListeners();
    this.updateIcon();
  }

  /**
   * Render toggle button
   */
  render() {
    if (!this.container) {
      // Create floating toggle button
      const toggle = document.createElement('button');
      toggle.className = 'dark-mode-toggle';
      toggle.id = 'darkModeToggle';
      toggle.setAttribute('aria-label', '  ');
      toggle.innerHTML = '<i class="fas fa-moon"></i>';
      document.body.appendChild(toggle);
      this.container = toggle;
    } else {
      this.container.innerHTML = `
        <button class="dark-mode-toggle-btn" id="darkModeToggle" aria-label="  ">
          <i class="fas fa-moon"></i>
        </button>
      `;
    }
  }

  /**
   * Setup event listeners
   */
  setupEventListeners() {
    const toggle = document.getElementById('darkModeToggle') || this.container;
    if (toggle) {
      toggle.addEventListener('click', () => {
        darkModeManager.toggle();
        this.updateIcon();
      });
    }

    // Listen for theme changes
    window.addEventListener('themechange', () => {
      this.updateIcon();
    });
  }

  /**
   * Update icon based on current theme
   */
  updateIcon() {
    const toggle = document.getElementById('darkModeToggle') || this.container;
    if (!toggle) return;

    const icon = toggle.querySelector('i');
    if (icon) {
      if (darkModeManager.isDarkMode) {
        icon.className = 'fas fa-sun';
        toggle.setAttribute('aria-label', '  ');
      } else {
        icon.className = 'fas fa-moon';
        toggle.setAttribute('aria-label', '  ');
      }
    }
  }
}

// Auto-initialize if container exists
if (document.getElementById('darkModeToggleContainer')) {
  new DarkModeToggle(document.getElementById('darkModeToggleContainer'));
}

// Export for manual initialization
export default DarkModeToggle;
