  handleLogout() {
    //       
    if (typeof authSystem !== 'undefined') {
      const token = localStorage.getItem('adminToken') || sessionStorage.getItem('adminToken');
      authSystem.logout(token);
    } else {
      localStorage.removeItem('adminToken');
      localStorage.removeItem('adminUser');
      sessionStorage.removeItem('adminToken');
      sessionStorage.removeItem('adminUser');
    }

    this.currentUser = null;
    this.currentRole = null;
    this.showLogin();
  }

  showLogin() {
    document.getElementById('loginScreen').style.display = 'flex';
    document.getElementById('dashboard').style.display = 'none';
  }

  showDashboard() {
    document.getElementById('loginScreen').style.display = 'none';
    document.getElementById('dashboard').style.display = 'block';

    // Update UI based on role
    this.updateUIForRole();

    //   
    if (typeof permissionsSystem !== 'undefined' && this.currentUser) {
      permissionsSystem.setUser(this.currentUser);
      permissionsSystem.updateUI();
    }

    // Load initial data
    this.loadDashboard();
    this.loadUsers();
    this.loadServices();

    // Show appropriate sections
    if (this.currentRole === 'super-admin') {
      document.querySelectorAll('.super-admin-only').forEach(el => {
        el.style.display = '';
      });
    }
  }

  updateUIForRole() {
    // Update user name and role
    document.getElementById('userName').textContent = this.currentUser.name;
    document.getElementById('userRole').textContent =
      this.currentRole === 'super-admin' ? ' ' : '';
    document.getElementById('userRole').className =
      `role-badge ${this.currentRole === 'super-admin' ? 'super-admin' : 'admin'}`;
  }

  checkPermission(section) {
    if (this.currentRole === 'super-admin') {
      return true; // Super admin has all permissions
    }

    const permissions = this.currentUser.permissions || [];

    // Check if user has permission for this section
    const sectionPermissions = {
      'dashboard': ['*', 'dashboard'],
      'users': ['*', 'users'],
      'admins': ['*'], // Only super admin
      'services': ['*', 'services'],
      'analytics': ['*', 'analytics'],
      'logs': ['*', 'logs'],
      'permissions': ['*'], // Only super admin
      'settings': ['*', 'settings']
    };

    const requiredPermissions = sectionPermissions[section] || [];
    return permissions.some(p => requiredPermissions.includes(p) || p === '*');
  }

  showSection(section) {
    // Hide all sections
    document.querySelectorAll('.content-section').forEach(sec => {
      sec.classList.remove('active');
    });

    // Remove active class from nav items
    document.querySelectorAll('.nav-item').forEach(item => {
      item.classList.remove('active');
    });

    // Show selected section
    const sectionElement = document.getElementById(`${section}Section`);
    if (sectionElement) {
      sectionElement.classList.add('active');
    }

    // Add active class to nav item
    const navItem = document.querySelector(`[data-section="${section}"]`);
    if (navItem) {
      navItem.classList.add('active');
    }

    // Update navigation builder active state
    import('./utils/navigation-builder.js').then(module => {
      const navigationBuilder = module.default;
      navigationBuilder.setActive(section);
    }).catch(() => {
      // Navigation builder not available, continue
    });

    this.currentSection = section;

    // Load section data
    this.loadSectionData(section);

    // Load advanced system if it's an advanced system
    if (section && section.startsWith('advanced-')) {
      this.loadAdvancedSystem(section);
    }

    // Load super admin data if super admin
    if (this.currentRole === 'super-admin' && typeof superAdminManager !== 'undefined') {
      this.loadSuperAdminSection(section);
    }
  }

