  setupAdvancedBackupTabs() {
    document.querySelectorAll('#advancedBackupSection .tab-btn').forEach(btn => {
      btn.addEventListener('click', () => {
        const tab = btn.getAttribute('data-tab');
        document.querySelectorAll('#advancedBackupSection .tab-btn').forEach(b => b.classList.remove('active'));
        document.querySelectorAll('#advancedBackupSection .tab-content').forEach(c => {
          c.classList.remove('active');
          c.style.display = 'none';
        });
        btn.classList.add('active');
        document.getElementById(`${tab}Tab`).classList.add('active');
        document.getElementById(`${tab}Tab`).style.display = 'block';
      });
    });
  }

