  loadSectionData(section) {
    switch (section) {
      case 'dashboard':
        this.loadDashboard();
        break;
      case 'users':
        this.loadUsers();
        break;
      case 'admins':
        if (this.currentRole === 'super-admin') {
          this.loadAdmins();
        }
        break;
      case 'services':
        this.loadServices();
        break;
      case 'analytics':
        this.loadAnalytics();
        break;
      case 'logs':
        this.loadLogs();
        break;
      case 'permissions':
        if (this.currentRole === 'super-admin') {
          this.loadPermissions();
        }
        break;
      case 'activity':
        this.loadActivity();
        break;
      case 'reports':
        this.loadReports();
        break;
      case 'audit':
        if (this.currentRole === 'super-admin') {
          this.loadAuditLogs();
        }
        break;
      case 'health':
        this.loadHealth();
        break;
      case 'backup':
        if (this.currentRole === 'super-admin') {
          this.loadBackups();
        }
        break;
      default:
        // Try to load advanced system
        if (section.startsWith('advanced-')) {
          this.loadAdvancedSystem(section);
        }
        break;
    }

    // Apply language after section load
    i18n.applyLanguage();
  }

  /**
   * Load advanced system dynamically
   */
  loadAdvancedSystem(sectionId) {
    try {
      const system = systemsRegistry.getSystem(sectionId);
      if (!system) {
        console.warn(`System ${sectionId} not found in registry`);
        return;
      }

      // Get or create container
      let container = document.getElementById(`${sectionId}Section`);
      if (!container) {
        // Create container if it doesn't exist
        const mainContent = document.getElementById('mainContent') || document.querySelector('.main-content') || document.querySelector('#dashboard .content');
        if (mainContent) {
          container = document.createElement('div');
          container.id = `${sectionId}Section`;
          container.className = 'content-section active';
          mainContent.appendChild(container);
        } else {
          console.error('Main content container not found');
          return;
        }
      }

      // Load CSS
      systemsRegistry.loadSystemCSS(sectionId);

      // Initialize system
      const systemInstance = systemsRegistry.initializeSystem(sectionId, container);
      if (systemInstance) {
        console.log(` Loaded system: ${system.name}`);
      } else {
        console.warn(` Failed to initialize system: ${sectionId}`);
      }
    } catch (error) {
      console.error(`Error loading system ${sectionId}:`, error);
    }
  }

