/**
 *  Navigation Builder -    
 *       30
 */

import systemsRegistry from './systems-registry.js';

class NavigationBuilder {
  constructor() {
    this.navContainer = null;
  }

  /**
   * Initialize navigation
   */
  init() {
    this.navContainer =
      document.querySelector('.sidebar .nav-list') ||
      document.querySelector('.navigation') ||
      document.querySelector('#navigation') ||
      document.querySelector('.nav-menu');

    if (!this.navContainer) {
      console.warn('Navigation container not found, creating one...');
      this.createNavigationContainer();
    }

    this.buildAdvancedSystemsNavigation();
  }

  /**
   * Create navigation container if it doesn't exist
   */
  createNavigationContainer() {
    const sidebar = document.querySelector('.sidebar') || document.querySelector('#sidebar');
    if (!sidebar) {
      console.error('Sidebar not found');
      return;
    }

    this.navContainer = document.createElement('div');
    this.navContainer.className = 'nav-list';
    sidebar.appendChild(this.navContainer);
  }

  /**
   * Build navigation items for all advanced systems
   */
  buildAdvancedSystemsNavigation() {
    if (!this.navContainer) {
      console.error('Navigation container not found');
      return;
    }

    // Get all systems from registry
    const systems = systemsRegistry.getAllSystems();

    // Group systems by category
    const categories = {
      ' ': [
        'advanced-sessions',
        'advanced-programs',
        'advanced-goals-plans',
        'advanced-appointments',
        'advanced-medications',
        'advanced-medical-records',
        'advanced-medical-reports',
        'advanced-follow-up',
        'advanced-evaluations',
      ],
      ' ': [
        'advanced-hr',
        'advanced-accounting',
        'advanced-inventory',
        'advanced-maintenance',
        'advanced-facilities',
      ],
      ' ': ['advanced-elearning', 'advanced-library', 'advanced-research'],
      ' ': [
        'advanced-events',
        'advanced-complaints',
        'advanced-quality',
        'advanced-security',
        'advanced-transportation',
        'advanced-restaurant',
        'advanced-volunteers',
        'advanced-partnerships',
        'advanced-pr',
        'advanced-funding',
      ],
      ' ': ['advanced-branches', 'advanced-call-center', 'advanced-referrals'],
    };

    // Build navigation HTML
    let navHTML = '';

    // Add category headers and items
    Object.keys(categories).forEach(categoryName => {
      const categorySystems = categories[categoryName];

      // Category header
      navHTML += `
        <div class="nav-category">
          <div class="nav-category-header">
            <span class="nav-category-title">${categoryName}</span>
          </div>
          <div class="nav-category-items">
      `;

      // Add systems in this category
      categorySystems.forEach(systemId => {
        const system = systems.find(s => s.id === systemId);
        if (system) {
          navHTML += this.createNavItem(system);
        }
      });

      navHTML += `
          </div>
        </div>
      `;
    });

    // Append to navigation container
    const existingAdvancedNav = this.navContainer.querySelector('.advanced-systems-nav');
    if (existingAdvancedNav) {
      existingAdvancedNav.remove();
    }

    const advancedNavWrapper = document.createElement('div');
    advancedNavWrapper.className = 'advanced-systems-nav';
    advancedNavWrapper.innerHTML = navHTML;
    this.navContainer.appendChild(advancedNavWrapper);

    // Add event listeners
    this.attachEventListeners();
  }

  /**
   * Create navigation item HTML
   */
  createNavItem(system) {
    return `
      <div class="nav-item" data-section="${system.id}">
        <i class="${system.icon}"></i>
        <span class="nav-item-text">${system.name}</span>
      </div>
    `;
  }

  /**
   * Attach event listeners to navigation items
   */
  attachEventListeners() {
    const navItems = this.navContainer.querySelectorAll('.advanced-systems-nav .nav-item');
    navItems.forEach(item => {
      item.addEventListener('click', e => {
        e.preventDefault();
        const section = item.getAttribute('data-section');

        // Remove active class from all items
        navItems.forEach(ni => ni.classList.remove('active'));

        // Add active class to clicked item
        item.classList.add('active');

        // Trigger section change
        if (window.dashboard) {
          window.dashboard.showSection(section);
        } else {
          // Fallback: dispatch custom event
          window.dispatchEvent(new CustomEvent('section-change', { detail: { section } }));
        }
      });
    });
  }

  /**
   * Update active navigation item
   */
  setActive(sectionId) {
    const navItems = this.navContainer.querySelectorAll('.advanced-systems-nav .nav-item');
    navItems.forEach(item => {
      if (item.getAttribute('data-section') === sectionId) {
        item.classList.add('active');
      } else {
        item.classList.remove('active');
      }
    });
  }
}

// Export singleton instance
const navigationBuilder = new NavigationBuilder();

// Auto-initialize when DOM is ready
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', () => {
    navigationBuilder.init();
  });
} else {
  navigationBuilder.init();
}

export default navigationBuilder;
